.list {
  list-style: none;

  margin: 0;
  padding: 0;
}

.sorting {
  border-bottom: 3px solid #b2d4d4;

  padding-bottom: 1rem;
  margin-bottom: 2rem;
}

.sorting button {
  font: inherit;
  color: teal;
  background-color: transparent;

  border: 1px solid teal;
  border-radius: 4px;

  padding: 0.5rem 1.5rem;
  cursor: pointer;
}

.sorting button:hover {
  background-color: #c2fafa;
}